/*nav utility*/

.page-nav {
  &__wrap {
    text-align: center;
  }

  &__nav {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;

    & > * {
      margin-bottom: 1rem;
    }
  }

  &__next,
  &__prev {
    font-size: 1.8rem;
    letter-spacing: 1px;
    font-weight: 500;
    text-transform: uppercase;

    &:before {
      font-family: 'Linearicons';
      font-size: 30px;
      font-weight: normal;
      display: inline-block;
      vertical-align: middle;
    }
  }

  &__prev {
    &:before {
      content: "\e93b";
      padding-right: 10px;
    }
  }

  &__next {
    &:after {
      content: "\e93c";
      padding-left: 10px;
    }
  }
}
